(100vw - 320px)/1680)}}@media(min-width: 760px){.input_block input::placeholder{font-size:calc(12px + 10*.0595238095vw)}}.input_block span{color:#fff;font-family:Commissioner;font-size:22px;font-style:normal;font-weight:400;line-height:154.3%}@media(max-width: 760px){.input_block span{font-size:calc(12px + 17*(100vw - 320px)/1680)}}@media(min-width: 760px){.input_block span{font-size:calc(12px + 10*.0595238095vw)}}.select_block label{display:flex;gap:14px}.select_block label select{width:30%;border-radius:4px;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-image:url("../../../../assets/svgIcons/ArrowDownRight (1).svg");background-repeat:no-repeat;background-position:right center;background-size:30px;padding:20px 30px}@media screen and (max-width: 767.98px){.select_block label select{width:100%;margin:10px 0 0}}@media(max-width: 760px){.select_block label select{font-size:calc(12px + 17*(100vw - 320px)/1680)}}@media(min-width: 760px){.select_block label select{font-size:calc(12px + 10*.0595238095vw)}}.select_block label select option{padding-right:20px}@media screen and (max-width: 767.98px){.select_block label{flex-direction:column}}.remember_me{display:flex;gap:10px;margin-top:10px;color:#fff;font-family:Commissioner;font-size:16px;font-style:normal;font-weight:400;line-height:154.3%}.remember_me label{position:relative;padding-left:30px;cursor:pointer}.remember_me label::before{content:"";display:flex;align-items:center;justify-content:center;width:20px;height:20px;border:1px solid #fff;border-radius:2px;position:absolute;left:0;top:0}.remember_me label.active::before{content:"✓"}.remember_me p{color:#fff;font-style:normal;font-weight:400}@media(max-width: 760px){.remember_me p{font-size:calc(8px + 13.6*(100vw - 320px)/1680)}}@media(min-width: 760px){.remember_me p{font-size:calc(8px + 8*.0595238095vw)}}.remember_me input[type=checkbox]{display:none}.form_contact_button{margin:22px 0 0;padding:10px 140px;border-radius:4px;color:#191923;text